<template>
  <v-app>
    <v-container>
        <v-card>
          <v-card-title></v-card-title>
          <v-card-subtitle>Register</v-card-subtitle>
          <v-card-text>
            <v-row>
              <v-col cols="5">
                <v-form ref="registerForm">
                  <v-text-field
                      v-model="username"
                      :rules="usernameRules"
                      label=""
                      name="username"
                      prepend-inner-icon="mdi-account-outline"
                      required
                      type="text"
                  ></v-text-field>
                  <v-text-field
                      id="password"
                      v-model="password"
                      :rules="passwordRules"
                      label=""
                      name="Password"
                      prepend-inner-icon="mdi-lock"
                      required
                      type="password"
                  ></v-text-field>
                  <v-text-field
                      id="confirmPassword"
                      v-model="confirmPassword"
                      :rules="confirmPasswordRules"
                      label=""
                      name="Confirm Password"
                      prepend-inner-icon="mdi-lock"
                      required
                      type="password"></v-text-field>
                </v-form>
              </v-col>
            </v-row>
          </v-card-text>
          <v-card-actions>
            <v-row>
              <v-col cols="3">
                <v-btn block color="info" v-bind:loading="loading" @click="submit"></v-btn>
              </v-col>
              <v-spacer></v-spacer>
            </v-row>
          </v-card-actions>
        </v-card>
      <v-snackbar
          v-model="snackbar"
      >
        {{ text }}

        <template v-slot:action="{ attrs }">
          <v-btn
              color="info"
              text
              v-bind="attrs"
              @click="snackbar = false"
          >
            Close
          </v-btn>
        </template>
      </v-snackbar>
    </v-container>
  </v-app>
</template>

<script>
import axios from '../api'

export default {
  name: "register",
  metaInfo: {
    title: ' - AWS Panel',
  },
  data() {
    return {
      text: '',
      snackbar: false,
      username: "",
      usernameRules: [
        v => !!v || "",
        v => /^[a-zA-Z0-9_-]{4,8}$/.test(v) || "4-8"
      ],
      password: '',
      passwordRules: [v => !!v || ""],
      confirmPassword: "",
      confirmPasswordRules: [
        v => !!v || "",
        v => v === this.password || ""
      ],
      loading: false,
    }
  },
  methods: {
    submit() {
      if (this.$refs.registerForm.validate()) {
        this.loading = true
        let data = new FormData();
        data.append("username", this.username)
        data.append("password", this.password)
        axios.post("/api/v1/User/Register",
            data).then((response) => {
          if (response.data.code === 200) {
            this.text = '...'
            setTimeout(() => {
              this.$router.push('/Login')
            }, 2000)
          } else {
            this.text = response.data.msg
          }
        }).catch((error) => {
          console.log(error)
          this.loading = false
        }).finally(() => {
          this.snackbar = true
          this.loading = false
        })
      }
    }
  }
};
</script>